.messageList {
  margin-top: 1vh;
  margin-bottom: 1vh;
  box-sizing: border-box;
  height: calc(77vh);
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.messageBubble {
  font-family: Arial, Helvetica, sans-serif;
  font-size: smaller;
  position: relative;
  padding: 2vw;
  width: 60vw;
  margin-bottom: -1em;
  border-radius: 10px;
  left: 0vw;
  color: white;
}
